  <div class="homePage">
    <!-- 第一页 -->
    <div class="section active">
      <!-- 全屏背景图 -->
      <div class="img-box">
        <hoverShowVideo ref="hoverShowVideo" videoOrGif="gif" />
      </div>
      <!-- 全屏背景图 -->

      <!-- 欢迎语 -->
      <div class="welcome-box positionCenter">
        <welcomeBox ref="welcomeBox" />
      </div>
      <!-- 欢迎语 -->

      <!-- 导航栏 -->
      <div :class="isCollapse ? 'menu-box-collapse' : 'menu-box'">
        <menuBox ref="menuBox" :isCollapse="isCollapse" />
      </div>
      <!-- 导航栏 -->
    </div>
    <!-- 第一页 -->

    <!-- 第二页 -->
    <!-- 项目经历 -->
    <div class="section">项目经历</div>
    <!-- 项目经历 -->
    <!-- 第二页 -->

    <!-- 第三页 -->
    <div class="section">
      <!-- 开源项目 -->
      <div class="open-box">开源项目</div>
      <!-- 开源项目 -->

      <!-- 友链 -->
      <div class="">友链</div>
      <!-- 友链 -->
    </div>
    <!-- 第三页 -->

    <!-- 输入密码的弹窗 -->
    <passwordDialog ref="passwordDialog" />
    <!-- 输入密码的弹窗 -->

    <!-- 快速回到顶部 -->
    <!-- 快速回到顶部 -->
