<script>
import { makePassword, checkPassword } from "@/utils/password";
import passwordDialog from "@/views/homePage/passwordDialog";
import menuBox from "@/views/homePage/menuBox";
import { mapActions } from "vuex";

export default {
  components: { passwordDialog, menuBox },
  data() {
    return {
      isCollapse: false,
    };
  },
  methods: {
    ...mapActions(["ADD_PASSWORD"]),

    handleScroll(e) {
      if (
        !this.$store.state.auth.isScroll &&
        !JSON.parse(localStorage.getItem("isScroll"))
      )
        this.$refs.passwordDialog.showDialog();
    },

    setPassword() {
      this.ADD_PASSWORD(makePassword());
    },

    changeIsCollapse() {
      this.isCollapse = !this.isCollapse;
    },

    initFullPage() {
      let that = this;
      console.log("initFullPage mounted");
      new fullpage(".homePage", {
        autoScrolling: true,
        scrollHorizontally: true,
        recordHistory: false,
        anchors: [
          "index?page=1",
          "index?page=2",
          "index?page=3",
          "index?page=4",
        ],
        sectionsColor: [null, "orange", "#ADD8E6", "lightGreen"],
        onLeave: function (origin, destination, direction) {
          that.handleScroll();
        },
      });
    },

    destroyFullPage() {
      fullpage_api.destroy("all");
    },

    moveTo(page) {
      fullpage_api.moveTo(page);
    },
  },

  created() {
    this.setPassword();
  },

  mounted() {
    this.initFullPage();
  },

  destroyed() {
    this.destroyFullPage();
  },
};
</script>

